.section-header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 100px;
  z-index: 10;
  position: relative;

  .section-title {
    margin-right: 15px;
  }
}

.section-header__links {
  @include list-reset;
  display: flex;
  flex-wrap: wrap;

  li {
    padding: 0 13px;
    border-right: 1px solid #efe8d8;

    a {
      @include font(16, 19);
      @include not-hover;
      padding: 10px 0;
      text-decoration: none;
      text-transform: lowercase;

      &:hover {
        @include hover;
        color: $golden;
      }

      &.active {
        color: $golden;
        text-decoration: underline;
        text-decoration-skip-ink: none;
        text-decoration-thickness: from-font;
        text-underline-offset: 2px;
        transition: $style-change-duration;
      }
    }

    &:last-child {
      border-right: 0;
    }
  }
}


@media (max-width: $desktop-width) {
  .section-header {
    margin-bottom: 50px;
  }
}

@media (max-width: $tablet-width) {
  .section-header {
    flex-direction: column;
    align-items: flex-start;

    .section-title {
      margin-right: 0;
      margin-bottom: 22px;
    }
  }

  .section-header__links {
    margin: 0;

    li {
      &:first-child {
        padding-left: 0;
      }
    }
  }
}
