.slider-btn {
  @include not-hover;
  position: absolute;
  z-index: 1;
  bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  padding: 5px 0;

  &.swiper-button-disabled {
    opacity: 0.3;
    cursor: auto;
  }

  &:hover {
    @include hover;
  }

  &--prev {
    left: -51px;
  }

  &--next {
    right: -51px;
    svg {
      transform: rotate(180deg);
    }
  }
}
