.events {
  margin-bottom: 100px;
  padding-top: 30px;

  .section-header {
    margin-bottom: 100px;
  }

  .section-title {
    .events__back-link {
      display: none;
    }
  }
}

.events__next {
  &.hidden {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    overflow: hidden;
  }
}

.events__container {
  position: relative;
}

.events__slider-wrapper {
  position: relative;
  max-width: 1146px;
  margin: 0 auto 90px;


  .slider-btn {
    top: 50%;
    transform: translateY(-50%);

    &::after {
      @include pseudo-elem;
      width: 25.5px;
      height: 16px;
      background-color: rgba(39, 40, 42, 0.85);
    }

    &--prev {
      left: -25.5px;

      &::after {
        right: 0;
      }
    }

    &--next {
      right: -25.5px;

      &::after {
        left: 0;
      }
    }
  }

  &.show-slide-mode {

    .events__slider-container {
      box-shadow: none;
    }

    .slider-btn {
      display: none;
    }
  }
}

.events__slider-container {
  overflow: hidden;
  box-shadow: 0 0 45px 0 rgba(0,0,0,.35);
}

.events__slider {
  @include list-reset;
  display: flex;
}

.events__slide {
  left: 300px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  min-height: 366px;
  border-radius: 2px;
  box-shadow: 0 0 45px 0 rgba(0, 0, 0, 0.35);
  transition-duration: 1s;
  transition-property: left;

  & > .button {
    display: none;
  }

  &.hidden {
    opacity: 0;
    visibility: hidden;
  }

  &.show-details {
    position: absolute;
    left: 0;

    .events__slide-content {
      z-index: 1;
    }

    .events__slide-details {
      opacity: 1;
      visibility: visible;
      left: calc(100% + 44px);
      transition-delay: 0.5s;
      transition-duration: 1.5s;
    }

    .events__details-link {
      display: none;
    }

    .events__slide-img {
      display: none;
    }
  }
}

.events__slide-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 32px 22px 24px;
  text-decoration: none;
  @include not-hover;
  color: #ffffff;
  background-color: rgba(39, 40, 42, 0.85);
  z-index: 1;

  &:hover {
    @include hover;
  }
}

.events__slide-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1;

  img {
    width: 100%;
    max-height: 198px;
    object-fit: cover;
    opacity: 0.75;
  }

  &::after {
    @include pseudo-elem;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: linear-gradient(180deg, rgba(39, 40, 42, 0.00) 0%, #27282a 100%);
  }
}

.events__slide-date {
  margin: 0;
  padding-bottom: 23px;
  @include font(16, 19);
  border-bottom: 1px solid rgba(239, 232, 216, 0.10);

  span {
    @include font(60, 70);
    font-weight: 200;
  }
}

.events__slide-day {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  margin-bottom: 20px;
  padding: 10px 0;
  @include font(16, 19);
  border-bottom: 1px solid rgba(239, 232, 216, 0.10);

  span {
    margin-right: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.events__slide-title {
  margin: 0;
  margin-bottom: 58px;
  @include font(20, 26);
  color: $golden;
}

.events__details-link {
  @include not-hover;
  @include button-reset;
  @include font(16, 19);
  margin-top: auto;
  font-weight: 300;
  text-align: left;
  color: #ffffff;
  text-transform: lowercase;

  &:hover {
    @include hover;
    color: $golden;
  }
}

.events__slide-details {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  height: 100%;
  width: 285px;

  p {
    margin: 0;
    margin-bottom: 69px;
    @include font(16, 19);
  }
}

.events__last {
  .events__slide {
    a {
      justify-content: flex-end;
      padding-bottom: 37px;
    }
  }

  .events__slide-date {
    padding-bottom: 20px;
    border-bottom: none;
  }

  .events__slide-title {
    margin-bottom: 0;
  }

  &.visually-hidden {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    overflow: hidden;
  }
}

.events__next-img-container {
  position: absolute;
  z-index: -1;
  right: 50px;
  top: 50%;
  transform: translateY(-50%);
  width: 571px;
  height: 751px;
  mix-blend-mode: lighten;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0;
    visibility: hidden;

    &.visible {
      opacity: 1;
      visibility: visible;
      transition-duration: 1s;
      transition-delay: 1s;
    }
  }
}

.events__last-img-container {
  position: absolute;
  z-index: -1;
  right: 50px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  width: 479px;
  height: 751px;
  padding-right: 40px;
  overflow: auto;
  opacity: 0;
  visibility: hidden;
  
    /* W3C standard
   сейчас только для Firefox */
   scrollbar-width: thin;
   scrollbar-color: #c4a478 #232325;
 
 
   /* для Chrome/Edge/Safari */
   &::-webkit-scrollbar {
     //height: 12px;
     width: 13px;
   }
 
   &::-webkit-scrollbar-track {
     background: #232325;
     border-radius: 20px;
   }
 
   &::-webkit-scrollbar-thumb {
     background-color: #c4a478;
     border-radius: 20px;
     border: 3px solid #232325;
   }
  
  img {
    width: 100%;
    height: auto;
    margin-bottom: 21px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &.visible {
    opacity: 1;
    visibility: visible;
    transition-duration: 1s;
    transition-delay: 1s;
    z-index: 0;
  }
}

.events__links {
  display: flex;
  justify-content: flex-start;
}

.events__back-link {
  padding: 10px 0;

  opacity: 0;
  visibility: hidden;

  &:hover {
    outline: none;
    cursor: pointer;
  }

  &.visible {
    opacity: 1;
    visibility: visible;
    transition-duration: 1s;
    transition-delay: 1s;
  }
}

@media (max-width: 1300px) {
  .events__next-img-container {
    width: 380px;
    height: 535px;
  }

  .events__last-img-container {
    width: 380px;
    height: 535px;
  }
}

@media (max-width: $desktop-width) {
  .events {
    margin-bottom: 70px;

    .section-title {
      display: flex;
      align-items: center;

      .events__back-link {
        margin-right: 20px;
        opacity: 1;
        visibility: visible;
      }
    }
  }

  .events__slider-wrapper {
    margin-bottom: 50px;
  }

  .events__next-img-container {
    position: static;
    width: 100%;
    height: auto;
    transform: none;
  }

  .events__last-img-container {
    flex-direction: row;
    width: 100%;
    height: auto;
    padding-right: 0;
    padding-bottom: 20px;
    transform: none;

    &.visible {
      position: static;
    }

    img {
      margin-bottom: 0;
      margin-right: 22px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  .events__links {
    .events__back-link {
      display: none;
    }
  }
}

@media (max-width: $tablet-width) {
  .events {
    margin-bottom: 40px;

    .section-header {
        margin-bottom: 60px;
      }
  }

  .events__slider-wrapper {
    margin-bottom: 50px;

    .slider-btn {
      display: none;
    }
  }

  .events__slide {
    left: 0;
    transform: none;

    &.hidden {
      position: absolute;
    }

    .events__slide-content {
      padding: 32px;
    }

    &.show-details {
      position: static;

      .events__slide-content {
        padding: 0;
      }

      & > .button {
        display: block;
        align-self: center;
        margin-bottom: 30px;
      }

      .events__slide-details {
        position: static;
        opacity: 1;
        visibility: visible;
      }

      .events__slide-title {
        margin-bottom: 30px;
      }

      .events__slide-details-img {
        opacity: 1;
        visibility: visible;
        position: static;
        transition-duration: 1s;
      }

      .events__details-link {
        display: none;
      }
    }
  }

  .events__slide-details {
    order: 1;
    opacity: 0;
    visibility: hidden;
    justify-content: flex-start;
    height: auto;
    width: 100%;

    p {
      margin-bottom: 0;
    }

    .button {
      display: none;
    }
  }

  .events__slide-title {
    margin-bottom: 50px;
  }

  .events__next-img-container {
    display: none;
  }

  .events__slide-details-img {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    margin-bottom: 30px;
    mix-blend-mode: lighten;
    width: 100%;
  }

  .events__last-img-container {
    img {
      opacity: 1;
      visibility: visible;
      position: static;
    }
  }
}
