html {
  box-sizing: border-box;
  height: 100%;
  font-weight: 300;
  font-family: "Roboto", "Arial", sans-serif;
  color: #efe8d8;
  background-color: #27282a;

  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  padding: 0;

  font-style: normal;
  font-size: 16px;
  line-height: 1.2;

  // background-image: url("../img/site-bg.png");
  // background-repeat: no-repeat;
  // background-size: cover;
  // background-position: top center;
}

.body--locked {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
