.link-down {
  position: relative;
  z-index: 3;
  display: flex;
  align-items: center;
  margin-top: auto;
  margin-left: -5px;
  width: fit-content;
  padding: 10px 0;
  color: $golden;
  text-decoration: none;
  text-transform: lowercase;

  svg {
    margin-right: 20px;
  }

  span {
    max-width: 100px;
  }

  &.hidden {
    visibility: hidden;
    opacity: 0;
    position: absolute;
  }
}

@media (max-width: $tablet-width) {
  .link-down {
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
  }
}
