@keyframes lightRotate {
  0% {
    left: 15%;
    top: 75px;
    width: 754px;
    height: 451px;
    border-radius: 754px;
    opacity: 0.7;
  }

  20% {
    left: 30%;
    top: 125px;
    transform: rotate(10.155deg) scale(1.1);
    opacity: 1;
  }

  40% {
    left: 50%;
    top: 160px;
    transform: rotate(60deg) scale(1.2);
    opacity: 0.8;
  }

  60% {
    left: 60%;
    top: 180px;
    transform: rotate(100.85deg) scale(1);
    opacity: 0.5;
  }

  80% {
    left: 40%;
    top: 140px;
    transform: rotate(135.85deg) scale(0.8);
    opacity: 0.8;
  }

  100% {
    left: 15%;
    transform: rotate(180deg) scale(1);
    top: 75px;
    width: 754px;
    height: 451px;
    border-radius: 754px;
    opacity: 0.7;
  }
}

.reservation {
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
  padding-top: 90px;
  margin-bottom: 100px;

  .section-title {
    margin: 0;

    &--mobile {
      display: none;
    }
  }

  &::after {
    @include pseudo-elem;
    background: linear-gradient(0deg, rgba(193, 161, 104, 0.50) 0%, rgba(193, 161, 104, 0.50) 100%), radial-gradient(50% 50% at 50% 50%, #C1A168 0%, #EFE8D8 46.35%, rgba(239, 232, 216, 0.00) 100%);
    filter: blur(75px);
    z-index: -1;
    animation: lightRotate 10s infinite linear;
    animation-fill-mode: forwards;
  }
}

.reservation__form {
  display: flex;
  flex-direction: column;
}

.reservation__form-content {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1145px;
  margin: 0 auto 50px;
  padding: 79px 100px;
  background-color: #27282a;
  box-shadow: 0 0 45px 0 rgba(0, 0, 0, 0.35);

  p {
    width: 37.2%;
    max-width: 352px;
    margin: 0;
    margin-right: 10.2%;
    @include font(20, 26);
  }
}

.reservation__form-fields {
  display: flex;
  flex-direction: column;
  width: 52.6%;

  input,
  textarea {
    @include font(16 ,19);
    @include not-hover;
    width: 100%;
    margin-bottom: 20px;
    padding: 15px 15px 16px 15px;
    background-color: #27282a;
    border: 1px solid rgba(196, 164, 120, 0.10);
    color: #efe8d8;

    &:last-child {
      margin-bottom: 0;
    }

    &::placeholder {
      @include font(16, 19);
      color: #efe8d8;
      opacity: 0.2;
    }

    @media (min-width: 1025px)  {
      &:hover {
        @include hover;
        border-color: $golden;
      }
    }

    &:focus {
      @include hover;
      border-color: $golden;
    }
  }

  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button {
    border-radius: 0;
    margin: 0;

    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
  }

  input[type=number]::-moz-inner-spin-button,
  input[type=number]::-moz-outer-spin-button {
    margin: 0;

    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
  }

  input[type=number]::-ms-inner-spin-button,
  input[type=number]::-ms-outer-spin-button {
    margin: 0;

    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
  }

  input[type=number] {
    outline: none;
    appearance: none;
    -webkit-appearance: none;
    border-radius: 0;
    -webkit-border-radius: 0;
    -moz-appearance: textfield;
  }

}

.reservation__form-submit {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 150px;

  .button {
    margin-bottom: 30px;
  }

  p {
    max-width: 580px;
    margin: 0;
    @include font(16, 19);
    color: $golden;
    text-align: center;
    opacity: 0.3;

    a {
      text-decoration: none;
    }
  }

  .link-down {
    position: absolute;
    right: 0;
    bottom: -10px;
    flex-direction: row-reverse;
    text-align: right;
    margin-right: -9px;

    svg {
      margin-right: 0;
      margin-left: 17px;
    }
  }
}

.reservation__form-success {
  width: 100%;
  max-width: 1145px;
  margin: 0 auto 50px;
  padding: 100px;
  background-color: #27282a;
  box-shadow: 0 0 45px 0 rgba(0, 0, 0, 0.35);

  p {
    max-width: 300px;
    margin: 0;
    margin-bottom: 20px;
    @include font(20, 26);

    &:last-child {
      margin-bottom: 0;
    }
  }
}

@media (max-width: $desktop-width) {

  .reservation {
    padding-top: 70px;
    margin-bottom: 70px;

    .section-title {
      display: none;

      &--mobile {
        display: block;
      }
    }
  }

  .reservation__form-content {
    padding: 50px 40px;
  }

  .reservation__form-success {
    padding: 50px;
  }
}

@media (max-width: $tablet-width) {
  .reservation {
    padding-top: 50px;
    margin-bottom: 40px;

    .section-header {
      margin-bottom: 36px;
    }
  }

  .reservation__form-content {
    flex-direction: column;
    margin-bottom: 30px;
    padding: 46px 17px;

    p {
      width: 100%;
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 60px;
      @include font(16, 19);
    }
  }

  .reservation__form-fields {
    width: 100%;
  }

  .reservation__form-submit {
    padding: 0;

    .button {
      width: 100%;
    }

    p {
      max-width: 100%;
    }

    .link-down {
      position: static;
      flex-direction: row;
      margin: 50px 0 auto;
      text-align: left;

      svg {
        margin-left: 0;
        margin-right: 26px;
      }
    }
  }

  .reservation__form-success {
    padding: 50px 34px 90px;

    p {
      margin-bottom: 20px;
    }
  }
}
