@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.interior {
  position: relative;
  padding-top: 30px;
  padding-bottom: 170px;

  .container {
    position: relative;
    z-index: 1;
  }
}

.interior__smoke-1 {
  position: absolute;
  left: 0;
  top: -100px;
  transition-duration: 2s;
  z-index: 1;
  max-width: 100%;
  background-image: url("../img/smoke-1.png");
  background-repeat: no-repeat;
  background-position: top left;
  background-size: contain;
  width: 1331px;
  height: 716px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/smoke-1@2x.png");
    background-size: 1331px;
  }

  &.animate {
    top: -35%;
  }
}

.interior__smoke-2 {
  position: absolute;
  right: 0;
  bottom: -200px;
  transition-duration: 2s;
  max-width: 100%;
  background-image: url("../img/smoke-2.png");
  background-repeat: no-repeat;
  background-position: top right;
  background-size: contain;
  width: 1770px;
  height: 1020px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/smoke-2@2x.png");
    background-size: 1770px;
  }

  &.animate {
    bottom: -40%;
    background-position: top right -200px;
  }
}

@media (max-width: $desktop-width) {
  .interior__smoke-1 {
    width: 80%;
  
    &.animate {
      top: -20%;
    }
  }

  .interior__smoke-2 {
    &.animate {
      bottom: -45%;
      background-position: top right -100px;
    }
  }
}

@media (max-width: $tablet-width) {
  .interior__smoke-1 {
    top: -40%;
    background-image: url("../img/smoke-1-mob.png");
    background-position: top center;
    background-size: cover;
    width: 100%;
    height: 629px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/smoke-1-mob@2x.png");
    }

    &.animate {
      top: -60%;
    }
  }

  .interior__smoke-2 {
    bottom: 0;
    background-image: url("../img/smoke-2-mob.png");
    background-position: top center;
    background-size: cover;
    width: 100%;
    height: 608px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/smoke-2@2x.png");
    }

    &.animate {
      bottom: -20%;
      background-position: top right;
    }
  }
}

.interior__content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.interior__left {
  width: 26.9%;
  margin-right: 2.1%;


  div {
    display: none;
    animation-duration: 1s;

    h2 {
      margin: 0 0 100px 31px;
      @include font(40, 47);
      font-weight: 200;
      color: $golden;
    }

    p {
      margin: 0;
      @include font(20, 26);
    }
  }
}

.interior__right {
  display: flex;
  flex-wrap: wrap;
  width: 71%;
}

.interior__slider-wrapper {
  position: relative;
  width: 58.5%;
}

.interior__slider-container {
  overflow: hidden;
}

.interior__slider {
  @include list-reset;
  display: flex;
}

.interior__slide {
  display: flex;
  flex-shrink: 0;

  img {
    width: 100%;
    height: 100%;
    max-height: 600px;
    object-fit: cover;
  }
}

.interior__next-slide-preview {
  position: relative;
  width: 20.75%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left top;
  filter: blur(2.5px);
  transition: $style-change-duration;

  &::after {
    @include pseudo-elem;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(92deg, #27282A -18.19%, rgba(39, 40, 42, 0.00) 115.23%);
  }
}

.interior__after-next-slide-preview {
  position: relative;
  width: 20.75%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left top;
  filter: blur(5px);
  transition: $style-change-duration;

  &::after {
    @include pseudo-elem;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(89deg, rgba(39, 40, 42, 0.00) -275.71%, #27282A 130.95%);
  }
}

@media (max-width: $desktop-width) {
  .interior__content {
    flex-direction: column;
  }

  .interior__left {
    order: 1;
    width: 100%;
    margin-right: 0;

    div {
      h2 {
        margin: 0 0 30px 0;
      }
    }
  }

  .interior__right {
    width: 100%;
    margin-bottom: 30px;
  }

  .interior__slider-wrapper {
    .slider-btn {
      display: none;
    }
  }
}

@media (max-width: $tablet-width) {
  .interior {
    padding-bottom: 70px;
  }

  .interior__left {
    div {
      p {
        @include font(16, 19);
      }
    }
  }

  .interior__slide {
    img {
      max-height: 316px;
    }
  }

  .interior__slider-wrapper {
    width: 88%;
  }

  .interior__next-slide-preview {
    width: 6%;
  }

  .interior__after-next-slide-preview {
    width: 6%;
  }
}
