.link-up {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  width: fit-content;
  margin-top: auto;
  margin-right: -9px;
  color: $golden;
  text-decoration: none;
  text-transform: lowercase;

  svg {
    margin-left: 17px;
    transform: rotate(180deg);
  }

  span {
    max-width: 100px;
    text-align: right;
  }
}

@media (max-width: $tablet-width) {
  .link-up {
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
  }
}
