@keyframes fadeOut {
  from {
    opacity: 1;
    visibility: visible;
  }

  to {
    opacity: 0;
    visibility: hidden;
  }
}

.menu {
  position: relative;
  margin-bottom: 35px;
  padding-top: 35px;

  .link-down {
    display: none;
  }
  
  .section-header {
    margin-bottom: 110px;
  }

  .section-title {
    .menu__back-link {
      display: none;
    }
  }
}

.menu__links {
  padding-bottom: 65px;
  background-image: url("../img/menu-links-bg.png");
  background-repeat: no-repeat;
  background-position: center bottom 45px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/menu-links-bg@2x.png");
    background-size: 1920px;
  }

  &::after {
    @include pseudo-elem;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #27282a;
    z-index: 1;
    animation-duration: 1s;
    animation-delay: 1s;
    animation-fill-mode: forwards;
    opacity: 1;
  }


  &.hidden-bg {
    &::after {
      animation-name: fadeOut;
    }
  }

  &.hidden {
    opacity: 0;
    visibility: hidden;
    position: absolute;
  }

  a {
    display: flex;
    justify-content: center;
    width: 100%;
    @include not-hover;
    z-index: 2;

    &:hover {
      @include hover;
    }

    &.blur {
      filter: blur(5px);
    }
  }
}

.menu__links__tip {
  display: none;
}

.menu__links-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
}

.menu__dark-img {
  position: absolute;
  z-index: 2;
  animation-duration: 1s;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  opacity: 1;
}

.menu__link--cigar {
  position: absolute;
  bottom: -3px;
  left: 50%;
  transform: translateX(-50%);
  @include not-hover;

  &::after {
    @include pseudo-elem;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 57px;
    background-image: url("../img/cigar-gradient.png");
    background-repeat: no-repeat;
    background-position: center;
  }
}

.menu__part {
  padding-bottom: 80px;
  background-repeat: no-repeat;
  opacity: 0;
  visibility: hidden;
  position: absolute;

  &.visible {
    opacity: 1;
    visibility: visible;
    position: static;
  }

  .container {
    display: flex;
    flex-wrap: wrap;
  }

  &--cigar {
    background-image: url("../img/menu-part-cigar-bg.png");

    background-position: left bottom;

    @media (max-width: 1600px) {
      background-position: left -100px bottom;
    }

    @media (max-width: 1400px) {
      background-size: 75%;
      background-position: left -70px bottom;
    }

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/menu-part-cigar-bg@2x.png");
      background-size: 1105px;

      @media (max-width: 1400px) {
        background-size: 75%;
      }
    }
  }

  &--wine {
    background-image: url("../img/menu-part-wine-bg.png");

    background-position: left bottom;

    @media (max-width: 1850px) {
      background-position: left -100px bottom;
    }

    @media (max-width: 1650px) {
      background-position: left -150px bottom;
    }

    @media (max-width: 1550px) {
      background-position: left -200px bottom;
    }

    @media (max-width: 1450px) {
      background-position: left -250px bottom;
    }

    @media (max-width: 1350px) {
      background-position: left -300px bottom;
    }

    @media (max-width: 1250px) {
      background-size: 90%;
      background-position: left -225px bottom 100px;
    }

    @media (max-width: 1100px) {
      background-size: 95%;
      background-position: left -210px bottom 100px;
    }

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/menu-part-wine-bg@2x.png");
      background-size: 1324px;

      @media (max-width: 1250px) {
        background-size: 90%;
      }
  
      @media (max-width: 1100px) {
        background-size: 95%;
      }
    }

    .menu__part-left {
      p {
        max-width: 262px;
        margin-top: auto;
        margin-left: auto;
        margin-bottom: 82px;
        color: #27282a;

        @media (max-width: 1250px) {
          margin-bottom: 145px;
        }

        @media (max-width: 1100px) {
          max-width: 215px;
          margin-bottom: 125px;
        }
      }
    }
  }
}

.menu__part-left {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 36.5%;
  margin-right: 4%;

  p {
    max-width: 332px;
    margin: 0;
    margin-left: 46px;
    @include font(20, 26);
    color: $golden;
  }
}

.menu__back-link {
  display: flex;
  justify-content: center;
  @include not-hover;
  margin-bottom: 50px;

  &:hover {
    @include hover;
  }
}

.menu__part-list {
  width: 59.5%;
  height: 564px;
  overflow: auto;

  /* W3C standard
   сейчас только для Firefox */
  scrollbar-width: thin;
  scrollbar-color: #c4a478 #232325;


  /* для Chrome/Edge/Safari */
  &::-webkit-scrollbar {
    //height: 12px;
    width: 13px;
  }

  &::-webkit-scrollbar-track {
    background: #232325;
    border-radius: 20px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #c4a478;
    border-radius: 20px;
    border: 3px solid #232325;
  }

  h3 {
    margin: 0;
    margin-bottom: 26px;
    @include font(20, 26);
    font-weight: 300;
    color: $golden;
  }

  table {
    width: 100%;
    height: 100%;
    border-collapse: collapse;
    border-spacing: 0;

    td {
      padding: 0;
      padding-bottom: 10px;

      vertical-align: top;

      &:first-child {
        padding-right: 30px;
      }

      &:last-child {
        padding-right: 35px;
      }
    }
  }
}

@media (max-width: $desktop-width) {
  .menu {
    .section-title {
      display: flex;
      align-items: center;

      .menu__back-link {
        margin-bottom: 0;
        margin-right: 20px;
      }
    }
  }

  .menu__part {
    padding-bottom: 405px;

    .container {
      flex-direction: column;
    }

    &--cigar {
      background-position: bottom;
      background-size: auto;
    }

    &--wine {
      padding-bottom: 570px;
      background-position: bottom;
      background-size: auto;
      background-image: url("../img/menu-part-wine-bg-tablet.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/menu-part-wine-bg-tablet@2x.png");
        background-size: 1024px;
      }
    }
  }

  .menu__part-left {
    display: none;
  }

  .menu__part-list {
    width: 100%;
    height: auto;

    table {
      td {
        &:last-child {
          text-align: end;
        }
      }
    }
  }
}

@media (max-width: $tablet-width) {
  .menu {

    .link-down {
      position: absolute;
      bottom: -146px;
      display: flex;
      margin-top: 86px;
    }

    .section-header {
      margin-bottom: 23px;
    }
  }

  .menu__links {
    position: relative;
    margin-bottom: 207px;
    padding: 0;
    background-position: center bottom -30px;
  }

  .menu__link--wine {

    img {
      width: 320px;
      height: 328px;
      object-fit: cover;
    }
  }

  .menu__link--cigar {

    img {
      //width: 320px;
      height: 187px;
      object-fit: cover;
    }
  }

  .menu__dark-img {
    width: 320px;
    height: 328px;
    object-fit: cover;
  }

  .menu__links__tip {
    display: block;
    max-width: 260px;
    margin: 0;
    margin-bottom: 66px;
    padding: 0 20px;
    @include font(20, 26);
    opacity: 0.2;
  }

  .menu__part {

    &--cigar {
      background-size: 180%;
      background-position: bottom 32px left -185px;
    }

    &--wine {
      background-image: url("../img/menu-part-wine-bg-mobile.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/menu-part-wine-bg-mobile@2x.png");
        background-size: 900px;
      }
    }
  }
}

@media (max-width: 500px) {
  .menu__part {

    &--cigar {
      background-size: 220%;
    }

    &--wine {
      padding-bottom: 460px;
      background-size: 150%;
    }
  }
}

@media (max-width: 400px) {
  .menu__part {

    &--cigar {
      background-size: 250%;
    }

    &--wine {
      padding-bottom: 450px;
      background-size: 180%;
    }
  }
}
