.filter {
  display: none;
  flex-direction: column;
  margin-bottom: 38px;
  border-top: 1px solid rgba(239, 232, 216, 0.10);
  border-bottom: 1px solid rgba(239, 232, 216, 0.10);

  &.active {

    .filter__header {
      border-bottom: 1px solid rgba(239, 232, 216, 0.10);

      &::after {
        transform: translateY(-50%) rotate(-180deg);
      }
    }

    .filter__parameters {
      display: flex;
    }
  }
}

.filter__header {
  @include not-hover;
  @include button-reset;
  @include font(16, 19);
  position: relative;
  width: 100%;
  height: 100%;
  padding: 10px 50px 10px 0;
  text-align: left;
  color: inherit;

  &::after {
    @include pseudo-elem;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    width: 25px;
    height: 25px;
    background-image: url("../img/icon-filter-arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    transition: $style-change-duration;
  }
}

.filter__parameters {
  display: none;
  flex-direction: column;

  label {

    span {
      @include font(16, 19);
      display: flex;
      width: 100%;
      padding: 10px 0;
      white-space: nowrap;
    }

    input {

      &:checked {
        & + span {
          color: $golden;
        }
       }
    }
  }
}

@media (max-width: $tablet-width) {
  .filter {
    display: flex
  }
}
