.header {
  position: relative;
  padding-bottom: 70px;
  background-image: url("../img/header-bg.png");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  height: 100vh;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/header-bg@2x.png");
  }
}

.header__slipping-bg {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100px;
    background-image: url("../img/slipping-bg.png");
    background-position: center top;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/slipping-bg@2x.png");
      background-size: 1920px;
    }
  }

.header__container {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-top: 236px;
  padding-bottom: 30px;
}

.header__open-menu {
  position: fixed;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 50;

  button {
    position: absolute;
    right: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    @include button-reset;
    @include not-hover;

    &:hover {
      @include hover;
    }
  }
}

.header__logo {
  max-width: 440px;
  margin-left: 225px;
  margin-bottom: 50px;
  //margin-bottom: 197px;

  img {
    margin-bottom: 45px;
  }
}

.header__title {
  margin: 0;
  @include font(40,47);
  font-weight: 200;
  text-transform: lowercase;
}

.header__nav {
  position: fixed;
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 100;
  padding-bottom: 50px;
  background-color: rgba(39, 40, 42, 0.9);
  opacity: 0;
  visibility: hidden;
  text-transform: lowercase;
  transition: $style-change-duration;
  overflow: auto;

  &.active {
    opacity: 1;
    visibility: visible;
  }
}

.header__nav-container {
  position: relative;
  padding-top: 97px;
}

.header__close-menu {
  position: absolute;
  right: 50px;
  top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  @include button-reset;
  @include not-hover;

  &:hover {
    @include hover;
  }
}

.header__nav-list {
  @include list-reset;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.header__nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;

  &:last-child {
    margin-bottom: 0;
  }

  a {
    @include not-hover;
    @include font(40, 47);
    font-weight: 200;
    text-decoration: none;
    text-align: center;

    &:hover {
      @include hover;
      color: $golden;
    }
  }

  &.active {

    & > a {
      margin-bottom: 30px;
      color: $golden;
      text-decoration: underline;
      text-decoration-skip-ink: none;
      text-decoration-thickness: from-font;
      text-underline-offset: 3px;
      transition: $style-change-duration;
    }

    .header__subnav-list {
      display: flex;
    }
  }
}

.header__subnav-list {
  @include list-reset;
  display: none;
  flex-direction: column;
  align-items: center;


  li {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    @include font(20, 26);
    font-weight: 300;
  }
}

.header__nav-reserv {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  @include font(20, 26);
  font-weight: 300;
  text-align: center;

  a {
    @include not-hover;
    margin-top: 5px;
    text-decoration: none;

    &:hover {
      @include hover;
      color: $golden;
    }
  }
}

@media (max-width: $desktop-width) {
  .header__logo {
    margin-left: 200px;
  }

  .header__open-menu {
    button {
      right: 30px;
    }
  }
}

@media (max-width: $tablet-width) {
  .header {
    background-image: url("../img/header-bg-mobile.png");
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
    padding-bottom: 62px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/header-bg-mobile@2x.png");
    }
  }

  .header__container {
    padding-top: 195px;
  }

  .header__open-menu {
    button {
      right: 20px;
    }
  }

  .header__close-menu {
    right: 20px;
  }

  .header__title {
    @include font(20, 26);
  }

  .header__logo {
    max-width: 165px;
    margin: 0 auto 50px;

    img {
      width: 150px;
      margin-bottom: 25px;
    }
  }

  .header__nav-item {
    margin-bottom: 25px;

    a {
      @include font(30, 37);
    }

    &.active {

      & > a {
        margin-bottom: 15px;
      }
    }
  }

  .header__subnav-list {

    li {
      margin-bottom: 10px;
    }

    a {
      @include font(18, 24);
    }
  }

  .header__nav-reserv {
    @include font(18, 24);
  }
}
