    margin-bottom: 70px;
  }
  .events .section-title {
    display: flex;
    align-items: center;
  }
  .events .section-title .events__back-link {
    margin-right: 20px;
    opacity: 1;
    visibility: visible;
  }

  .events__slider-wrapper {
    margin-bottom: 50px;
  }

  .events__next-img-container {
