    margin-bottom: 60px;
  }

  .events__slider-wrapper {
    margin-bottom: 50px;
  }
  .events__slider-wrapper .slider-btn {
    display: none;
  }

  .events__slide {
    left: 0;
    transform: none;
  }
  .events__slide.hidden {
    position: absolute;
  }
  .events__slide .events__slide-content {
    padding: 32px;
  }
  .events__slide.show-details {
    position: static;
  }
  .events__slide.show-details .events__slide-content {
    padding: 0;
  }
  .events__slide.show-details > .button {
    display: block;
    align-self: center;
    margin-bottom: 30px;
  }
  .events__slide.show-details .events__slide-details {
    position: static;
    opacity: 1;
    visibility: visible;
  }
  .events__slide.show-details .events__slide-title {
    margin-bottom: 30px;
  }
  .events__slide.show-details .events__slide-details-img {
    opacity: 1;
    visibility: visible;
    position: static;
    transition-duration: 1s;
  }
  .events__slide.show-details .events__details-link {
    display: none;
  }

  .events__slide-details {
    order: 1;
    opacity: 0;
    visibility: hidden;
    justify-content: flex-start;
    height: auto;
    width: 100%;
  }
  .events__slide-details p {
    margin-bottom: 0;
  }
  .events__slide-details .button {
    display: none;
  }

  .events__slide-title {
    margin-bottom: 50px;
  }

  .events__next-img-container {
    display: none;
  }

  .events__slide-details-img {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    margin-bottom: 30px;
    mix-blend-mode: lighten;
    width: 100%;
  }

  .events__last-img-container img {
    opacity: 1;
    visibility: visible;
    position: static;
  }
}
@keyframes lightRotate {
  0% {
    left: 15%;
    top: 75px;
    width: 754px;
    height: 451px;
    border-radius: 754px;
    opacity: 0.7;
  }
  20% {
    left: 30%;
    top: 125px;
    transform: rotate(10.155deg) scale(1.1);
    opacity: 1;
  }
  40% {
    left: 50%;
    top: 160px;
    transform: rotate(60deg) scale(1.2);
    opacity: 0.8;
  }
  60% {
    left: 60%;
    top: 180px;
    transform: rotate(100.85deg) scale(1);
    opacity: 0.5;
  }
  80% {
    left: 40%;
    top: 140px;
    transform: rotate(135.85deg) scale(0.8);
    opacity: 0.8;
  }
  100% {
    left: 15%;
    transform: rotate(180deg) scale(1);
    top: 75px;
    width: 754px;
    height: 451px;
    border-radius: 754px;
    opacity: 0.7;
  }
}
.reservation {
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
  padding-top: 90px;
  margin-bottom: 100px;
}
.reservation .section-title {
  margin: 0;
}
.reservation .section-title--mobile {
  display: none;
}
.reservation::after {
  content: "";
