  background: linear-gradient(0deg, rgba(193, 161, 104, 0.5) 0%, rgba(193, 161, 104, 0.5) 100%), radial-gradient(50% 50% at 50% 50%, #C1A168 0%, #EFE8D8 46.35%, rgba(239, 232, 216, 0) 100%);
  filter: blur(75px);
  z-index: -1;
  animation: lightRotate 10s infinite linear;
  animation-fill-mode: forwards;
}

