.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-image: url("../img/modal-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .modal {
    background-image: url("../img/modal-bg@2x.png");
  }
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1000px;
  width: 100%;
  padding: 85px;
  background-color: rgba(39, 40, 42, 0.95);
  border-radius: 150px;
}
.modal__content span {
  margin-bottom: 30px;
  font-size: 130px;
  line-height: 152px;
  color: #c4a478;
}
.modal__content .button {
  margin-bottom: 60px;
  font-size: 20px;
  line-height: 26px;
  color: #efe8d8;
}
.modal__content p {
  font-size: 16px;
  line-height: 19px;
  margin: 0;
  text-align: center;
}
.modal__close-btn {
  position: absolute;
  top: -60px;
  right: 36px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: transparent;
  border: none;
  cursor: pointer;
}
.modal__close-btn:not(:hover) {
  transition: 0.3s;
}
@media (min-width: 1025px) {
  .modal__close-btn:hover {
    outline: none;
    cursor: pointer;
    transition: 0.3s;
  }
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

@media (max-width: 767px) {
  .modal__wrapper {
    padding: 116px 20px;
  }
  .modal__content {
    padding: 52px 12px 63px;
    border-radius: 20px;
  }
  .modal__content span {
    font-size: 120px;
    line-height: 140px;
  }
  .modal__content .button {
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .modal__content p {
    max-width: 200px;
  }
  .modal__close-btn {
    top: -86px;
    left: 50%;
    transform: translateX(-50%);
  }
}