.contacts {
  padding-top: 100px;
}

.contacts__top {
  display: flex;
  flex-direction: column;
  align-items: center;

  img {
    margin-bottom: 100px;
  }
}

.contacts__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  margin: 0 30px 100px;
}

.contacts__socials {
  @include list-reset;
  width: 142px;

  li {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }

    a {
      @include not-hover;

      &:hover {
        @include hover;
      }
    }
  }
}

.contacts__contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 330px;

  p {
    margin: 0;
    margin-bottom: 10px;
    @include font(20, 26);
    text-align: center;
  }

  a {
    @include font(20, 26);
    text-decoration: none;
  }

  .contacts__route-link {
    display: flex;
    align-items: center;
    margin-top: 50px;
    @include font(16, 19);
    color: $golden;
    text-decoration: none;
    @include not-hover;

    &:hover {
      @include hover;
    }

    svg {
      margin-left: 26px;
      transform: rotate(180deg);
    }
  }
}

.contacts__nav-list {
  @include list-reset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 50px;
}

.contacts__nav-item {
  margin-right: 46px;

  &:last-child {
    margin-right: 0
  }

  a {
    padding: 10px 0;
    @include not-hover;
    @include font(40, 47);
    font-weight: 200;
    opacity: 0.2;
    text-decoration: none;
    text-transform: lowercase;

    &:hover {
      @include hover;
      opacity: 1;
    }
  }
}

@media (max-width: $desktop-width) {
  .contacts {
    padding-top: 70px;
  }

  .contacts__top {
    img {
      margin-bottom: 60px;
    }
  }

  .contacts__nav-item {
    a {
      @include font(30, 37);
    }
  }
}

@media (max-width: $tablet-width)  {
  .contacts {
    padding-top: 50px;
  }

  .contacts__top {
    .link-up {
      display: none;
    }

    img {
      width: 181px;
    }
  }

  .contacts__content {
    flex-direction: column;
    align-items: center;
    margin: 0;
    margin-bottom: 60px;
  }

  .contacts__socials {
    display: none;
  }

  .contacts__contacts {
    max-width: 250px;

    p {
        margin-bottom: 15px;
    }
  }

  .contacts__nav-list {
    flex-direction: column;
    margin-bottom: 60px;
  }

  .contacts__nav-item {
    margin-right: 0;
    margin-bottom: 20px;
    text-align: center;

    &:last-child {
        margin-bottom: 0;
    }

    a {
      @include font(40, 47);
    }
  }
}
