.footer {
  margin-top: auto;
  padding: 20px 0 21px;
  background-color: #222325;

  .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    p {
      margin: 0;
      margin-right: 30px;
      @include font(14, 16);
      color: rgba(239, 232, 216, 0.2);
    }

    span {
      @include font(16, 19);
      color: #7b7b75;
    }
  }
}

@media (max-width: $tablet-width)  {
  .footer {
    padding: 21px 0 15px;

    .container {
      flex-direction: column;

      p {
        margin-bottom: 30px;
        margin-right: 0
      }

      span {
        align-self: flex-end;
      }
    }
  }
}
