.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-image: url("../img/modal-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
  transform $trans-modal,
  visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/modal-bg@2x.png");
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: center;

    max-width: 1000px;
    width: 100%;
    padding: 85px;

    background-color: rgba(39, 40, 42, 0.95);
    border-radius: 150px;

    span {
      margin-bottom: 30px;
      @include font(130, 152);
      color: $golden;
    }

    .button {
      margin-bottom: 60px;
      @include font(20, 26);
      color: #efe8d8;
    }

    p {
      @include font(16, 19);
      margin: 0;
      text-align: center;
    }
  }

  &__close-btn {
    @include not-hover;
    position: absolute;
    top: -60px;
    right: 36px;
    z-index: 1;

    width: 40px;
    height: 40px;
    padding: 0;

    background: transparent;
    border: none;
    cursor: pointer;

    @media (min-width: 1025px) {
      &:hover {
        @include hover;
      }
    }
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }

}

@media (max-width: $tablet-width) {
  .modal {
    &__wrapper {
      padding: 116px 20px;
    }

    &__content {
      padding: 52px 12px 63px;
      border-radius: 20px;

      span {
        @include font(120, 140);
      }

      .button {
        width: 100%;
        padding-top: 20px;
        padding-bottom: 20px;
      }

      p {
       max-width: 200px;
      }
    }

    &__close-btn {
      top: -86px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
